@import url('https://fonts.googleapis.com/css2?family=EB+Garamond&family=Fira+Mono&family=Libre+Barcode+39+Text&display=swap');


.Movie {

    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "Fira Mono";

    .Backdrop {

        height: 40vh;
        min-height: 400px;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 50px;

        img {
            height: 100%;
        }

        .EdgeBlur {
            position: absolute;
            height: 100%;
            width: 100%;
            box-shadow: 0 0 80px 150px inset #272727;
        }
    }

    .Description {
        display: flex;

        img {
            height: 40vh;
            min-width: auto;
            margin: 0 40px;
            border-radius: 5px;
        }

        .Title {
            display: flex;
            align-items: baseline;

            h1 {
                font-family: "EB Garamond";
                color: white;
                font-size: 70px;
                margin-right: 20px;
            }

            h2 {
                font-size: 30px;
                margin-left: 20px;
                color: #afafaf;
            }

            h3 {
                font-size: 20px;
                color: #afafaf;
            }

        }

        .About {

            width: 40vw;

            h2 {
                color: #afafaf;
                font-size: 20px;
                margin: 30px 0px;
            }

        }

    }



}